select.form-select {
  width: max-content;
}

.divider {
  background-color: #ccc;
  margin: 8px 0; /* Задайте нужные отступы */
}

.category.pointer::after {
  content: ' *';
  color: red;
}

div[data-key="checkbox-109"] {
  cursor: pointer;
}

.sidebar {
  display: block;
  width: 100%;
  /* margin-top: -24px; */
  height: 160px;
  position: relative;
  transition: margin-top 0.3s ease;
  background-color: #ccc;
}

.sidebar.open {
  margin-top: -160px;
}

.checkbox {
  width: 20px;
  height: 20px;
}

.checkbox-container {
  width: 100%;
  height: 100%;
}

.company-name .btn.btn-secondary {
  width: 100%;
  box-sizing: border-box;
}

.sidebar.open .btn-open .bi-arrow-bar-up::before {
  rotate: 180deg;
}

.btn-open {
  position: absolute;
  bottom: -34px;
  right: 10px;
  width: 50px;
  border-radius: 0 0 5px 5px;
  height: 30px;
  margin: 0;
  padding: 0;
  background-color: rgba(128, 128, 128, 0.288);
}

.btn-open:hover {
  background-color: rgb(141, 141, 141);
}

.btn:first-child:active,
.btn-open:active {
  background-color: rgb(112, 112, 112);
  border: none;
}

.btn-open .bi::before {
  color: white;
  transition: rotate 0.3s ease;
}

.spinner {
  position: fixed;
  width: 100%;
  height: 70%;
}

.spinner .spinner-border {
  width: 120px;
  height: 120px;
}